import React from 'react';
import { useStaticQuery, graphql, Link } from 'gatsby';
import styled from 'styled-components';
import { Section, Container } from '@styles/global';
import ExternalLink from '@utils/externalLink';
import Twitter from '@static/icons/twitter.svg';
import Linkedin from '@static/icons/linkedin.svg';
import Facebook from '@static/icons/facebook.svg';

import { ReactComponent as Logo } from '@static/nfront-icon.svg';

const SOCIAL = [
    {
        icon: Facebook,
        link: '/',
    },
    {
        icon: Linkedin,
        link: '/',
    },
    {
        icon: Twitter,
        link: '/',
    },
];

const FooterWrapper = styled.footer`
    font-size: 12px;
    font-weight: 500;
    text-transform: uppercase;
    letter-spacing: 1px;
    color: var(--alt-color);
    background-color: var(--footer-color);

    a {
        color: var(--yellow);
    }
`;

const StyledContainer = styled(Container)`
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 2rem 1.5rem;

    @media (min-width: ${props => props.theme.screen.sm}) {
        flex-direction: row;
        justify-content: space-between;
    }

    ${props =>
        props.fluid &&
        `
        max-width: 100%;
    `};
`;

const SocialIcons = styled.div`
    display: flex;

    img {
        margin: 0 8px;
        width: 24px;
        height: 24px;
    }

    @media (max-width: ${props => props.theme.screen.sm}) {
        margin-top: 30px;
    }
`;

const Copyright = styled.div``;

const GRID = styled(Container)`
    display: flex;
    flex-direction: column;
    justify-content: space-between;

    @media (min-width: ${props => props.theme.screen.sm}) {
        flex-direction: row;
    }
`;

const StyledSection = styled(Section)`
    background: var(--footer-alt-color);
    padding: 3rem 0 !important;

    p {
        color: #6c757d;
    }

    .label {
        font-size: 14px;
        color: var(--accent-color);
    }

    ul {
        list-style-type: none;
        margin-left: 0;

        li {
            margin-bottom: 5px;
        }
    }

    svg {
        width: 50px;
        fill: var(--yellow);
    }
`;

export default function Footer(props) {
    const data = useStaticQuery(graphql`
        query {
            site {
                siteMetadata {
                    title
                }
            }
        }
    `);
    return (
        <FooterWrapper {...props}>
            <StyledSection>
                <GRID>
                    <div>
                        <p className="label">Headquarters</p>
                        <p>
                            Tollbugata 24, 0157
                            <br />
                            Oslo, Norway
                        </p>
                        <p>
                            Los Angeles, CA
                            <br />
                            Warsaw, Poland
                        </p>
                    </div>
                    <div>
                        <p className="label">Services</p>
                        <ul>
                            <li>
                                <Link to="/venture-capital/">
                                    Venture Capital
                                </Link>
                            </li>
                            <li>
                                <Link to="/outsource-development/">
                                    Outsourced Development
                                </Link>
                            </li>
                            <li>
                                <Link to="/hire-top-developer-teams/">
                                    Hire Top Developers
                                </Link>
                            </li>
                            <li>
                                <Link to="/corporate-innovation/">
                                    Corporate Innovation
                                </Link>
                            </li>
                        </ul>
                    </div>
                    <div>
                        <p className="label">Legal</p>
                        <ul>
                            <li>
                                <Link to="/contact/">Contact Us</Link>
                            </li>
                        </ul>
                    </div>
                    <div>
                        <Logo />
                    </div>
                </GRID>
            </StyledSection>
            <StyledContainer>
                <Copyright>
                    &copy; {new Date().getFullYear()}. Built for{' '}
                    <Link to="/">{data.site.siteMetadata.title}</Link> by{' '}
                    <ExternalLink href="https://www.adamidea.com/">
                        ADAMIDEA
                    </ExternalLink>
                </Copyright>
                <SocialIcons>
                    {SOCIAL.map(({ icon, link }) => (
                        <ExternalLink key={icon} href={link}>
                            <img src={icon} alt="link" />
                        </ExternalLink>
                    ))}
                </SocialIcons>
            </StyledContainer>
        </FooterWrapper>
    );
}
