import React, { useState } from 'react';
import { useStaticQuery, graphql, Link } from 'gatsby';
import AnchorLink from 'react-anchor-link-smooth-scroll';
import Scrollspy from 'react-scrollspy';
import { useIsHome } from '@utils/useIsHome';
import { useIsScroll } from '@utils/useIsScroll';

/*
import { ReactComponent as Logo } from '@static/logo.svg';
*/

import {
    NavContainer,
    Nav,
    NavList,
    NavItem,
    Logo,
    Mobile,
    Hamburger,
} from '@styles/navstyles.js';

const ListLink = props => (
    <NavItem>
        <Link to={props.to}>{props.children}</Link>
    </NavItem>
);

const mainMenu = ['About', 'Services', 'Portfolio'];
const secondaryMenu = [
    {
        name: 'About',
        path: '/#about',
    },
    {
        name: 'Capital',
        path: '/venture-capital/',
    },
    {
        name: 'Software',
        path: '/outsource-development/',
    },
    {
        name: 'Team',
        path: '/',
    },
    {
        name: 'Mentors',
        path: '/',
    },
    {
        name: 'Media',
        path: '/',
    },
    {
        name: 'Contact',
        path: '/contact/',
    },
];

export default function Navbar(props) {
    /** open dropdown menu on mobile */
    const [isMenuOpen, setIsMenuOpen] = useState(false);
    const toggleClassName = isMenuOpen ? 'hamburger is-open' : 'hamburger';
    const changeMenuColor = isMenuOpen ? 'is-open' : '';
    /** render different menu for other pages except home */
    const { isHome } = useIsHome();
    /** change navbar background on scroll */
    const { navBackground } = useIsScroll();

    const Menu = () => {
        return (
            <>
                {isHome ? (
                    <Scrollspy
                        items={mainMenu.map(navItem => navItem.toLowerCase())}
                        currentClassName="active"
                    >
                        {mainMenu.map(navItem => (
                            <NavItem key={navItem}>
                                <AnchorLink
                                    onClick={() => setIsMenuOpen(false)}
                                    href={`#${navItem.toLowerCase()}`}
                                >
                                    {navItem}
                                </AnchorLink>
                            </NavItem>
                        ))}
                        <ListLink to="/contact/">Contact</ListLink>
                    </Scrollspy>
                ) : (
                    <ul>
                        {secondaryMenu.map(({ name, path }) => {
                            return (
                                <NavItem key={name}>
                                    <Link to={path}>{name}</Link>
                                </NavItem>
                            );
                        })}
                    </ul>
                )}
            </>
        );
    };

    const data = useStaticQuery(graphql`
        query {
            site {
                siteMetadata {
                    title
                }
            }
        }
    `);
    return (
        <Nav
            {...props}
            className={changeMenuColor}
            style={{
                backgroundColor: navBackground
                    ? 'var(--alt-blue)'
                    : 'transparent',
            }}
        >
            <NavContainer>
                <Logo>
                    <Link to="/">{data.site.siteMetadata.title}</Link>
                </Logo>
                <NavList desktop>
                    <Menu />
                </NavList>
            </NavContainer>
            <Mobile>
                <Hamburger
                    className={toggleClassName}
                    onClick={() => setIsMenuOpen(!isMenuOpen)}
                >
                    <span></span>
                    <span></span>
                    <span></span>
                </Hamburger>

                {isMenuOpen && (
                    <NavList>
                        <Menu />
                    </NavList>
                )}
            </Mobile>
        </Nav>
    );
}
